@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
:root {
  --basic-white: #ffffff;
  --hover-white: rgba(255, 255, 255, 0.3);
  --background-secondary: #f2f2f2;
  --background-ternary: #eaeaea;
  --background-input: #e7e7e7;
  --accent-color: #68b738;
  --hover-accent: #5eaa2f;
  --active-accent: #9ecc82;
  --hero-green-1: #64a73c;
  --hero-green-2: #62a63a;
  --text-basic: #000000;
  --text-secondary: #444444;
  --text-secondary-hover: rgba(68, 68, 68, 0.3);
  --text-special: #111111;
  --text-products: #222222;
  --border-color-1: #cdcdcd;
  --border-color-2: #e6e6e6;
  --border-color-3: #d9d9d9;
  --borfer-color-4: #bababa;
  --slider-control: #666666;
  --special-grey: #666666;
  --status-error: #ff8282;
}

.visually-hidden {
  position: fixed;
  transform: scale(0);
}

.container {
  box-sizing: border-box;
  width: 100%;
  margin-inline: auto;
  padding-inline: 20px;
  position: relative;
}
@media (min-width: 576px) {
  .container {
    padding-inline: 30px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 708px;
    padding-inline: 0;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}
@media (min-width: 1440px) {
  .container {
    max-width: 1220px;
  }
}

.page__body {
  margin: 0;
  font-family: "Lato", "Arial", "Helvetica", sans-serif;
  font-size: 14px;
  line-height: 18px;
  background-color: var(--basic-white);
  color: var(--text-secondary);
  position: relative;
}

.main-header {
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .main-header {
    padding-block-start: 18px;
  }
}
@media (min-width: 1200px) {
  .main-header {
    width: 100%;
    padding-block-start: 55px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
    background-color: transparent;
  }
}

.main-header__container {
  padding-inline: 0;
}
@media (min-width: 768px) {
  .main-header__container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
}

.main-header__wrapper {
  min-height: 65px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-inline: 20px;
  position: relative;
  z-index: 3;
  background-color: var(--basic-white);
}
@media (min-width: 768px) {
  .main-header__wrapper {
    padding-inline: 0;
  }
}
@media (min-width: 1200px) {
  .main-header__wrapper {
    background-color: transparent;
  }
}

.logo-header {
  display: block;
  transition: opacity 0.3s;
  contain: content;
}
@media (min-width: 768px) {
  .logo-header {
    display: flex;
    gap: 3px;
  }
}
.logo-header[href] {
  cursor: pointer;
}
.logo-header[href]:hover {
  opacity: 0.8;
}
.logo-header[href]:active {
  opacity: 0.6;
}

.logo-header__picture {
  display: block;
  padding-block: 10px;
  padding-inline: 10px;
  margin-inline-start: -10px;
}
@media (min-width: 768px) {
  .logo-header__picture {
    padding-block: 0;
    padding-inline: 0;
    margin-inline-start: 0;
  }
}
@media (min-width: 1200px) {
  .logo-header__picture {
    margin-block-start: -6px;
  }
}

.logo-header__text {
  display: block;
  padding-block: 14px;
  padding-inline: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media (min-width: 768px) {
  .logo-header__text {
    position: static;
    transform: translate(0, 0);
  }
}
@media (min-width: 1200px) {
  .logo-header__text {
    width: 118px;
    height: 21px;
    margin-block-start: -1px;
  }
}

.main-navigation {
  font-family: "Oswald", "Arial", "Helvetica", sans-serif;
  font-size: 20px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;
  color: var(--text-basic);
  margin-inline: -20px;
}
@media (min-width: 768px) {
  .main-navigation {
    margin-inline: 0;
  }
}

.main-navigation__menu {
  text-align: center;
  border-bottom: 1px solid var(--border-color-2);
}
@media (min-width: 768px) {
  .main-navigation__menu {
    border-bottom: none;
  }
}

.menu-toggle {
  box-sizing: content-box;
  display: flex;
  flex-direction: column;
  row-gap: 6px;
  width: 24px;
  height: 21px;
  padding-block: 10px;
  padding-inline: 10px;
  margin-inline-end: -10px;
  background-color: var(--basic-white);
  position: relative;
  cursor: pointer;
  border: none;
}
.menu-toggle--opened .menu-toggle__line--1 {
  transform: rotate(45deg);
  top: 10px;
}
.menu-toggle--opened .menu-toggle__line--2 {
  display: none;
}
.menu-toggle--opened .menu-toggle__line--3 {
  transform: rotate(-45deg);
  top: 2px;
}
.menu-toggle--nojs {
  display: none;
}
@media (min-width: 768px) {
  .menu-toggle {
    display: none;
  }
}

.menu-toggle__line {
  width: 24px;
  height: 2px;
  background-color: var(--text-basic);
  position: relative;
  transition: transform 0.5s ease;
}

.site-menu {
  max-width: 100%;
  list-style: none;
  padding: 0;
  margin: 0;
  background-color: var(--basic-white);
  position: absolute;
  transform: translateY(-100%);
  left: 0;
  right: 0;
  z-index: 2;
}
@media (min-width: 1200px) {
  .site-menu--index {
    color: var(--basic-white);
  }
}
.site-menu--opened {
  position: absolute;
  transform: translateY(0);
  left: 0;
  right: 0;
  z-index: 2;
  box-shadow: -4px 2px 10px 0 var(--text-secondary);
}
.site-menu--animated {
  transition: transform 0.6s ease-out;
}
.site-menu--nojs {
  position: initial;
  box-shadow: none;
}
@media (min-width: 768px) {
  .site-menu {
    max-width: 462px;
    display: flex;
    flex-wrap: wrap;
    row-gap: 16px;
    column-gap: 16px;
    margin-block-start: 11px;
    margin-inline-end: -10px;
    background-color: transparent;
    position: initial;
    transform: none;
  }
}
@media (min-width: 1200px) {
  .site-menu {
    min-width: 475px;
    max-width: 502px;
  }
}

.site-menu__item {
  box-sizing: border-box;
  padding-block: 12px;
  border-top: 1px solid var(--border-color-2);
}
@media (min-width: 768px) {
  .site-menu__item--current::after {
    content: "";
    display: block;
    position: initial;
    width: calc(100% - 20px);
    height: 2px;
    background-color: var(--accent-color);
    margin-inline: auto;
    margin-block-start: -4px;
  }
}
@media (min-width: 1200px) {
  .site-menu__item--current::after {
    margin-block-start: 0;
  }
}
@media (min-width: 768px) {
  .site-menu__item {
    max-width: min-content;
    text-wrap: balance;
    padding-block: 0;
    border-top: none;
  }
}

.site-menu__link {
  display: inline-block;
  padding-block: 10px;
  padding-inline: 10px;
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  transition: opacity 0.3s;
}
.site-menu__link[href]:hover, .site-menu__link[href]:focus {
  opacity: 0.6;
}
.site-menu__link[href]:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .site-menu__link {
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1200px) {
  .site-menu__link {
    font-size: 20px;
    line-height: 20px;
  }
}

@media (min-width: 1200px) {
  .site-menu--index .site-menu__item--current::after {
    background-color: var(--basic-white);
  }
}
@media (min-width: 768px) {
  .site-menu--index .site-menu__item {
    padding-block: 0;
    border-top: none;
  }
}

.button {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 280px;
  min-height: 40px;
  padding-block: 7px;
  font-family: "Oswald", "Arial", "Helvetica", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  border: none;
  cursor: pointer;
  transition: background-color 0.5s;
}
@media (min-width: 768px) {
  .button {
    width: 250px;
    padding-block: 10px;
  }
}
.button--primary {
  color: var(--basic-white);
  background-color: var(--accent-color);
}
.button--primary:not(:disabled):hover, .button--primary[href]:hover, .button--primary:not(:disabled):focus, .button--primary[href]:focus {
  background-color: var(--hover-accent);
}
.button--primary:not(:disabled):active, .button--primary[href]:active {
  color: var(--hover-white);
}
.button--secondary {
  color: var(--text-secondary);
  background-color: var(--background-secondary);
}
.button--secondary:not(:disabled):hover, .button--secondary:not(:disabled):focus {
  background-color: var(--border-color-2);
}
.button--secondary:not(:disabled):active {
  color: var(--text-secondary-hover);
}

.contacts {
  position: relative;
}
@media (min-width: 1200px) {
  .contacts {
    display: grid;
    align-items: start;
    justify-items: start;
  }
}

.contacts__container {
  padding-inline: 0;
}
@media (min-width: 768px) {
  .contacts__container {
    margin-inline: 0;
  }
}
@media (min-width: 1200px) {
  .contacts__container {
    grid-row: 1/1;
    grid-column: 1/1;
    z-index: 1;
    margin-block: 95px;
    margin-inline: auto;
    pointer-events: none;
  }
}

.contacts__wrapper {
  box-sizing: border-box;
  width: 100vw;
  display: flex;
  justify-content: space-between;
  padding-block: 28px;
  padding-inline: 20px;
  background-color: var(--background-secondary);
}
.contacts__wrapper--index {
  padding-block: 26px;
  background-color: var(--basic-white);
}
@media (min-width: 768px) {
  .contacts__wrapper {
    padding-block: 60px;
    padding-inline: 70px;
  }
}
@media (min-width: 1200px) {
  .contacts__wrapper {
    max-width: 565px;
    padding-inline: 80px;
    padding-block: 60px 58px;
    background-color: var(--basic-white);
  }
}

.contacts__description {
  max-width: 140px;
  margin: 0;
  font-family: "Oswald", "Arial", "Helvetica", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;
  color: var(--text-special);
}
@media (min-width: 768px) {
  .contacts__description {
    max-width: 165px;
    font-size: 20px;
    line-height: 26px;
  }
}
@media (min-width: 1200px) {
  .contacts__description {
    margin-block-start: 1px;
  }
}

.contacts__address {
  max-width: 140px;
  line-height: 20px;
  font-style: normal;
}
@media (min-width: 768px) {
  .contacts__address {
    max-width: 157px;
    font-size: 16px;
    line-height: 20px;
    text-align: end;
  }
}
@media (min-width: 1200px) {
  .contacts__address {
    text-align: start;
  }
}

.contacts__city {
  display: block;
}
@media (min-width: 768px) {
  .contacts__city {
    margin-block-start: 20px;
  }
}
@media (min-width: 1200px) {
  .contacts__city {
    margin-block-start: 19px;
  }
}

.contacts__map {
  grid-row: 1/1;
  grid-column: 1/1;
}

.map {
  width: 100%;
  height: 362px;
  background-color: var(--background-ternary);
}
@media (min-width: 768px) {
  .map {
    min-height: 400px;
  }
}
@media (min-width: 1200px) {
  .map {
    height: 100%;
  }
}

.map__iframe {
  width: 100%;
  height: 100%;
  border: none;
}

.map__fallback-img {
  position: absolute;
  z-index: -1;
  left: 50%;
  transform: translateX(-50%);
}

.logo-footer {
  display: flex;
  justify-content: center;
  transition: opacity 0.3s;
}
.logo-footer[href] {
  cursor: pointer;
}
.logo-footer[href]:hover {
  opacity: 0.8;
}
.logo-footer[href]:active {
  opacity: 0.6;
}

.logo-footer__text {
  display: block;
  padding-block: 10px;
  padding-inline: 10px;
}
@media (min-width: 768px) {
  .logo-footer__text {
    margin-inline-start: -9px;
    margin-block-start: -5px;
  }
}

.main-footer {
  padding-block: 25px;
  background-color: var(--background-secondary);
}
@media (min-width: 768px) {
  .main-footer {
    padding-block: 45px 41px;
  }
}
@media (min-width: 1200px) {
  .main-footer {
    padding-block: 56px 53px;
  }
}

@media (min-width: 768px) {
  .main-footer__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
}

.main-footer__logo-footer {
  margin-block: 5px 10px;
  margin-inline: auto;
}
@media (min-width: 768px) {
  .main-footer__logo-footer {
    margin-block: 11px 0;
    margin-inline: 0;
  }
}

.main-footer__developer {
  margin-block-start: 11px;
}
@media (min-width: 768px) {
  .main-footer__developer {
    margin-block-start: 15px;
  }
}

.socials__list {
  max-width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px 24px;
  list-style: none;
  padding: 0;
  padding-block: 10px;
  margin: 0;
  border-top: 1px solid var(--border-color-3);
  border-bottom: 1px solid var(--border-color-3);
}
@media (min-width: 768px) {
  .socials__list {
    max-width: 150px;
    margin-inline-start: 27px;
    margin-block-start: -2px;
    border: none;
  }
}

.socials__link {
  display: block;
  width: 28px;
  height: 22px;
  padding-block: 10px;
}
.socials__link--vkontakte::after, .socials__link--youtube::after, .socials__link--telegram::after {
  content: "";
  display: block;
  position: absolute;
  width: 28px;
  height: 22px;
  background-color: var(--slider-control);
  transition: background-color 0.5s;
}
.socials__link--vkontakte:hover::after, .socials__link--youtube:hover::after, .socials__link--telegram:hover::after {
  background-color: var(--accent-color);
}
.socials__link--vkontakte:active::after, .socials__link--youtube:active::after, .socials__link--telegram:active::after {
  background-color: var(--accent-color);
  opacity: 0.3;
}
.socials__link--vkontakte::after {
  mask: url("../img/icons/sprite.svg#vkontakte") no-repeat center;
}
.socials__link--youtube::after {
  mask: url("../img/icons/sprite.svg#youtube") no-repeat center;
}
.socials__link--telegram::after {
  mask: url("../img/icons/sprite.svg#telegram") no-repeat center;
}

.developer {
  position: relative;
}

.developer__link {
  display: flex;
  justify-content: space-between;
  padding-block: 10px;
  padding-inline-end: 55px;
  font-size: 16px;
  line-height: 26px;
  text-decoration: none;
  color: inherit;
}
@media (min-width: 768px) {
  .developer__link {
    column-gap: 23px;
    justify-content: stretch;
    max-width: 170px;
    line-height: 20px;
    padding-inline-end: 0;
    margin-block-start: -8px;
  }
}
.developer__link::after {
  content: "";
  display: block;
  position: initial;
  width: 27px;
  height: 34px;
  background-color: var(--slider-control);
  mask: url("../img/icons/sprite.svg#htmlacademy") no-repeat center;
  margin-inline-end: -55px;
  margin-block-start: -3px;
  transition: background-color 0.5s;
}
@media (min-width: 768px) {
  .developer__link::after {
    margin-inline-end: 0;
    margin-block-start: -6px;
  }
}
.developer__link:hover::after {
  background-color: var(--accent-color);
}
.developer__link:active::after {
  background-color: var(--accent-color);
}
.developer__link:active {
  opacity: 0.3;
}

.hero {
  box-sizing: border-box;
  display: grid;
  align-items: start;
}
.hero::before {
  content: "";
  min-height: 302px;
  grid-row: 1/4;
  grid-column: 1/2;
  background-image: url("../img/hero/hero-bg-mobile.jpg"), linear-gradient(to right, var(--hero-green-1), var(--hero-green-2));
  background-position: top;
  background-size: contain;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .hero::before {
    content: none;
  }
}
@media (min-resolution: 2dppx) {
  .hero::before {
    background-image: url("../img/hero/hero-bg-mobile@2x.jpg"), linear-gradient(to right, var(--hero-green-1), var(--hero-green-2));
  }
}
@media (min-width: 768px) {
  .hero {
    background: var(--basic-white);
  }
}
@media (min-width: 1200px) {
  .hero {
    min-height: 694px;
    padding-block-start: 114px;
    background-image: url("../img/hero/hero-bg-desktop.jpg"), linear-gradient(to right, var(--basic-white) 50%, var(--hero-green-2) 50%);
    background-repeat: no-repeat;
    background-position: bottom right;
    background-size: contain, 100%;
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .hero {
    background-image: url("../img/hero/hero-bg-desktop@2x.jpg"), linear-gradient(to right, var(--basic-white) 50%, var(--hero-green-2) 50%);
  }
}

.hero__container {
  grid-row: 1/-1;
  grid-column: 1/2;
  display: grid;
  align-items: start;
}
@media (min-width: 768px) {
  .hero__container {
    justify-content: start;
    padding-block-start: 30px;
    padding-inline-start: 90px;
  }
}
@media (min-width: 1200px) {
  .hero__container {
    grid-template-columns: max-content 1fr;
    grid-auto-rows: min-content;
    padding-inline-start: 80px;
  }
}

.hero__title {
  grid-row: 1/2;
  grid-column: 1/2;
  max-width: 250px;
  margin: 0;
  margin-block: 27px 25px;
  margin-inline: auto;
  font-family: "Oswald", "Arial", "Helvetica", sans-serif;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  text-align: center;
  color: var(--basic-white);
}
@media (min-width: 768px) {
  .hero__title {
    max-width: 500px;
    margin-block-end: 40px;
    margin-inline: 0;
    font-size: 60px;
    line-height: 60px;
    text-align: left;
    color: var(--text-basic);
  }
}
@media (min-width: 1200px) {
  .hero__title {
    max-width: 500px;
    margin-block: 81px 40px;
    z-index: 1;
  }
}

.hero__decription {
  grid-row: 2/3;
  grid-column: 1/2;
  max-width: 250px;
  margin: 0;
  margin-block-end: 29px;
  margin-inline: auto;
  font-family: "Oswald", "Arial", "Helvetica", sans-serif;
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center;
  color: var(--basic-white);
}
@media (min-width: 768px) {
  .hero__decription {
    max-width: 300px;
    margin-block-end: 50px;
    margin-inline: 0;
    font-size: 20px;
    line-height: 20px;
    text-align: left;
    color: var(--text-basic);
  }
}
@media (min-width: 1200px) {
  .hero__decription {
    max-width: 500px;
    margin-block-end: 52px;
    z-index: 1;
  }
}

.hero__product {
  width: 100%;
  grid-row: 3/4;
  grid-column: 1/2;
  margin-inline: auto;
  margin-block-end: 3px;
}
@media (min-width: 768px) {
  .hero__product {
    grid-column: 1/3;
    width: 709px;
    margin-inline-start: -90px;
  }
}
@media (min-width: 1200px) {
  .hero__product {
    grid-column: 2/3;
    grid-row: 1/5;
    width: 552px;
    margin-inline-start: -160px;
    z-index: 0;
  }
}

.hero__product-image {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: contain;
}
@media (min-width: 768px) {
  .hero__product-image {
    width: 709px;
    margin-block-start: -29px;
  }
}
@media (min-width: 1200px) {
  .hero__product-image {
    width: 552px;
    margin-block-start: 0;
    margin-inline-start: 27px;
