.form__radio:focus + .form__control {
  outline: 2px solid var(--text-secondary);
}

.form__checkbox:checked + .form__control::after {
  content: "";
  display: block;
  position: absolute;
  width: 18px;
  height: 14px;
  background-color: transparent;
  background-image: url("../img/icons/sprite.svg#check-mark");
  background-repeat: no-repeat;
  background-size: contain;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.form__checkbox:focus + .form__control {
  outline: 2px solid var(--text-secondary);
}

.field {
  display: flex;
  align-items: center;
  column-gap: 13px;
  max-width: 280px;
  position: relative;
}
@media (min-width: 768px) {
  .field {
    max-width: 100%;
  }
}
@media (min-width: 1200px) {
  .field {
    flex-grow: 1;
  }
}
.field--email::after {
  content: "";
  display: block;
  position: absolute;
  width: 20px;
  height: 20px;
  background-color: var(--text-basic);
  right: 16px;
  mask-image: url("../img/icons/sprite.svg#email");
}
.field--email:focus-within::after {
  background-color: var(--accent-color);
}
.field--phone::after {
  content: "";
  display: block;
  position: absolute;
  width: 20px;
  height: 20px;
  background-color: var(--text-basic);
  right: 16px;
  mask-image: url("../img/icons/sprite.svg#phone");
}
.field--phone:focus-within::after {
  background-color: var(--accent-color);
}
.field--error .field__input {
  border: 2px solid var(--status-error);
}
.field--error::after {
  background-color: var(--status-error);
}

.field__label {
  flex-shrink: 0;
}
@media (min-width: 768px) {
  .field__label {
    font-size: 20px;
    line-height: 20px;
  }
}
.field__label--text-input {
  max-width: 35%;
}

.field__input {
  box-sizing: border-box;
  width: 100%;
  flex-grow: 1;
  padding-block: 13px;
  padding-inline: 15px 40px;
  font-family: "Oswald", "Arial", "Helvetica", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  text-transform: uppercase;
  border: 1px solid var(--background-input);
  color: var(--text-secondary);
}
.field__input:hover {
  border: 1px solid var(--border-color-1);
}
.field__input:focus {
  outline: 2px solid var(--text-secondary);
}
@media (min-width: 768px) {
  .field__input {
    font-size: 20px;
    max-height: 52px;
    padding-inline-start: 20px;
  }
}